.component-emoji-result-row {
  border-bottom: 1px solid #ccc;
  padding: 10px;
  height: 32px;
  position: relative;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  cursor: pointer;
}

.component-emoji-result-row-2 {
  height: 98px !important;
}

.component-emoji-result-row:hover {
  background-color: #eee;
}

.component-emoji-result-row img {
  width: 32px;
  height: 32px;
  padding-right: 10px;
}

.item {
  float: left;
  width: 75%;
}

.component-emoji-result-row .title {
  font-size: 18px;
  /*position: relative;*/
}

.component-emoji-result-row .descrip {
  font-size: 13px;
  font-style: italic;
  color:#999;
  /*position: relative;*/
}

.component-emoji-result-row .info {
  float: right;
  position: relative;
  /*top: 8px;*/
  right: 10px;
  color: #999;
  display: inline-block;
}

.component-emoji-result-row:hover .info {
  display: inline-block;
}
